.imageChart {
    background-image: url('img.png');
    width: 100%;
    background-size: 100%;
    background-repeat: no-repeat;
    height: 100%;
}
.wrapperLine {
    background-color: white;
    border-radius: 30px;
}
.wrapperChartLine {
    border-radius: 30px;
    background-color: #DFE0F0;
    display: inline-block;
}
.textHeader {
    padding: 20px;
    font-weight: 700;
    font-size: 17px;
    margin-bottom: 20px;
}
.recharts-cartesian-axis-tick-line {
    display: none;
}
.recharts-cartesian-axis-line {
    display: none;
}
.recharts-tooltip-cursor {
    display: none;
}
.recharts-cartesian-axis-ticks {
    font-size: 10px !important;
    color: #020202 !important;
}
.wrapperHover {
    height: 100px;
    width: 90px;
    background-color: none;
    box-shadow: 0px 3px 6px 0px #221F3F59;
    width: 57px;
    padding: 2px;
    height: 142px;
    position: relative;
    top: -40px;
    left: -38px;
    visibility: visible;
    border-radius: 16px;
}
.value {
    font-size: 12px;
    font-weight: 800;
    color: #FDD84C;
    text-align: center;
    padding-top: 10px;
}
.label {
    font-size: 10px;
    color: #544CA6;
    font-weight: 800;
    background-color: white;
    position: relative;
    top: 92px;
    text-align: center;
    border-radius: 16px;
}
.styleRow {
    margin-top: 20px;
    margin-right: 15px;
    margin-left: 15px;
    margin-bottom: 20px;
}
h2 {
    font-size: 24px;
}
.textNotifi {
    font-size: 14px;
}
.textRed {
    color: red;
}
.textBlue {
    color: blue;
    font-weight: 500;
}
@media (max-width: 577px) {
   .imageChart {
       height: 155px;
       background-size: 50%;
       background-position: center;
   }
}




